<template>
    <div>
        <form role="form" @submit.prevent class="search">
            <i class="fa fa-search" aria-hidden="true"></i>
            <input v-model="search" type="text" class="form-control">
        </form>
        <br> 
      <table class="table table-bordered accordion" id="accordion">
          <thead>
                <tr>
                    <th></th>
                    <th>  /  </th>
                    <th> </th>
                    <th width="30px"></th>
                    <th width="30px"></th>
                </tr>
          </thead>
          
          <tbody class="card" v-for="(item, index) in filteredList" :key="index">
                <tr>
                    <td data-toggle="collapse" :data-target="'#collapse'+(index+1)">
                        {{index+1}}
                    </td>
                    <td>{{ item.name }}</td>
                    <td>{{ item.type }}</td>
                    <td>
                        <a href="#" @click="unarchiveMember(item.id)"><i class="fa fa-2x fa-check-circle" aria-hidden="true"></i></a>                
                    <td>
                        <a href="#" @click="deleteMember(item.id)"><i class="fa fa-2x fa-trash" aria-hidden="true"></i></a>
                    </td>
                </tr>
                <tr id="collapse1" class="collapse "  data-parent="#accordion">
                    <td colspan="5" class="card-body">
                        Anim pariatur cliche reprehenderit, enim eiusmod high life accusamus terry richardson ad squid. 3 wolf moon officia aute, non cupidatat skateboard dolor brunch. Food truck quinoa nesciunt laborum eiusmod. Brunch 3 wolf moon tempor, sunt aliqua put a bird on it squid single-origin coffee nulla assumenda shoreditch et. Nihil anim keffiyeh helvetica, craft beer labore wes anderson cred nesciunt sapiente ea proident. Ad vegan excepteur butcher vice lomo. Leggings occaecat craft beer farm-to-table, raw denim aesthetic synth nesciunt you probably haven't heard of them accusamus labore sustainable VHS.
                    </td>
                </tr>
            </tbody>
      </table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                members: [],
                search: '',
            };
        },

        created () {
            this.getFullList();
        },
        computed: {
            filteredList() {
                return this.members.filter(members => {
                    return members.name.toLowerCase().includes(this.search.toLowerCase()) || members.type.toLowerCase().includes(this.search.toLowerCase())
                        || members.file || members.video || members.passport_photo || members.in_file
                })
            }
        },
        methods: {

	        getFullList() {
		        axios.get('/get-archive-members')
			        .then((response) => {
				        this.members = [];
				        response.data.forEach(member => {
					        if (member.solo_duet.length == 0 && member.status == "archive") {
						        this.members.push({
							        name: member.group.name,
							        file: member.group.file,
							        video: member.presentation.video,
							        type: member.app_type.name,
							        id: member.application_id
						        })
					        } else if (member.solo_duet.length == 1 && member.status == "archive") {
						        this.members.push({
							        name: `${member.solo_duet[0].name} ${member.solo_duet[0].surname} ${member.solo_duet[0].patronymic}`,
							        passport_photo: member.solo_duet[0].passport_photo,
							        in_file: member.solo_duet[0].in_file,
							        video: member.presentation.video,
							        type: member.app_type.name,
							        id: member.application_id
						        })
					        } else if (member.solo_duet.length == 2 && member.status == "archive") {
						        this.members.push({
							        name: `${member.solo_duet[0].name} ${member.solo_duet[0].surname} ${member.solo_duet[0].patronymic}, ${member.solo_duet[1].name} ${member.solo_duet[1].surname} ${member.solo_duet[1].patronymic}`,
							        passport_photo: `${member.solo_duet[0].passport_photo}, ${member.solo_duet[1].passport_photo}`,
							        in_file: `${member.solo_duet[0].in_file}, ${member.solo_duet[1].in_file}`,
							        video: member.presentation.video,
                                    type: member.app_type.name,
							        id: member.application_id
						        })
					        }
				        });
			        })
	        },
	        unarchiveMember(id) {
		        axios.post('/unarchive-members/' + id)
			        .then((response) => {
				        if (response.status == 200) {
					        this.getFullList();
				        }
				        swal("   ", {
					        icon: "success",
				        });


                    })
                    .catch((error) => {
                        swal({
                            icon: "error",
                            title: '',
                            text: '  '
                        });
                    });
            },
	        deleteMember(id) {
                swal({
                    title: " ?",
                    text: "  ",
                    content: "input",
                    icon: "warning",
                    buttons: true,
                    dangerMode: true,
                    // showCancelButton: true,
                    // closeOnConfirm: false,
                }).then( (inputValue) => {

                    if (inputValue === false) return false;
                    else if (inputValue === "") {
                        swal.showInputError("   ");
                        return false
                    }
                    else if (inputValue){
                        console.log(inputValue);
                        const data = {
                            message: inputValue,
                        };
                        axios.post('/delete-members/' + id, data)
                            .then((response) => {
                                if (response.status == 200) {
                                    this.getFullList();
                                }
                                swal("   ", {
                                    icon: "success",
                                });
                            })
                            .catch((error) => {
                                swal({
                                    icon: "error",
                                    title: '',
                                    text: ' '
                                });
                            });
                    }
                })



		        // swal({
			    //     title: " ?",
			    //     text: "       ",
			    //     icon: "warning",
			    //     buttons: true,
			    //     dangerMode: true,
		        // })
			    //     .then((willDelete) => {
				//         if (willDelete) {
				// 	        axios.post('/delete-members/' + id)
				// 		        .then((response) => {
				// 			        if (response.status == 200) {
				// 				        this.getFullList();
				// 			        }
				// 			        swal("   ", {
				// 				        icon: "success",
				// 			        });
				// 		        })
				// 		        .catch((error) => {
				// 			        swal({
				// 				        icon: "error",
				// 				        title: '',
				// 				        text: ' '
				// 			        });
				// 		        });
				//         }
			    //     })
	        }
        }
    }
</script>